const commentByReviewId = comments.reduce((acc, curr) => {
  return {
    ...acc,
    [curr.review_id]: [
      ...(acc[curr.review_id] ?? []),
      {
        id: curr.id,
        target_id: curr.target_id,
        parent_id: curr.parent_id,
        nickname: curr.nickname,
        regdt: curr.regdt,
        contents: curr.contents,
        depth: curr.depth,
        target_nickname: curr.target_nickname,
      },
    ],
  };
}, {});

export const initialData = datas.map(data => ({
  id: data.id,
  regdt: data.regdt,
  nickname: data.nickname,
  contents: data.contents,
  point: data.point,
  like: data.like,
  thumbnail: `https://i.balaan.io/review/${data.thumbnail}`,
  img: data.img.map((i) => `https://i.balaan.io/review/${i}`),
  comment: commentByReviewId[data.id] ?? [],
}));
